/* STATIC STYLES (desktop-first) */

.hero {
  position: relative;

  width: 100vw;
  height: 100vh;

  overflow: hidden;

  background-image: url('../../../public/hero_background.png');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.hero-content {
  position: absolute;
  top: 8rem;
  left: 50%;
  z-index: 2;

  display: flex;
  flex-direction: row;
  align-items: flex-start;

  padding: 0 2rem;

  transform: translateX(-50%);

  gap: 2rem;
}

.hero-marker {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero-marker .dot {
  width: 20px;
  height: 20px;

  border-radius: 50%;

  background-color: var(--color-secondary-dark);
}

.hero-marker .line {
  width: 4px;
  height: 320px;

  background: linear-gradient(to bottom, var(--color-secondary-dark), transparent);
}

.hero-text h1 {
  white-space: nowrap;
  color: var(--color-primary-text);
}

.hero-text h3 {
  margin-top: 0.5rem;

  white-space: nowrap;
  color: var(--color-primary-text-dark);
}

.scroll-indicator {
  position: absolute;
  bottom: 2rem;
  z-index: 2;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
}

.scroll-box {
  position: relative;

  display: flex;
  justify-content: center;
  align-items: flex-start;

  width: 35px;
  height: 64px;
  padding: 8px;

  border: 4px solid var(--color-primary-text-dark);
  border-radius: 24px;
}

.scroll-dot {
  width: 12px;
  height: 12px;

  border-radius: 50%;

  background-color: var(--color-primary-text-dark);

  animation: bounce 1.5s infinite;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(24px);
  }
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .hero-marker .line {
    height: 160px;
  }

  .scroll-indicator {
    bottom: 1rem;
  }
}

@media (max-width: 576px) {
  .hero-content {
    gap: 1rem;
  }
}
